*,
*::before,
*::after {
  box-sizing: border-box;
}
* {
  margin: 0;
}

html,
body {
  height: 100%;
}
body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}
input,
button,
textarea,
select {
  font: inherit;
  outline: none;
  border: none;
}
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

.Container {
  width: 1268px;
  margin: 0 auto;
  min-height: 100vh;
  position: relative;
  transition: 1s;
}

.LightTheme {
  background-color: white;
  color: black;
}
.DarkTheme {
  background-color: rgba(0, 0, 0, 0.911);
  color: white;
}

@media screen and (max-width: 821px) {
  .Container {
    width: 100vw;
  }
  .Header-Container,
  .Body-Container,
  .SkillsAndProjects {
    width: 80% !important;
  }
  .MySkills-Details {
    width: 100% !important;
    justify-content: space-between !important;
  }
}

@media screen and (max-width: 415px) {
  .Header-Container,
  .Body-Container {
    width: 90% !important;
  }
  .SkillsAndProjects,
  .MySkills-Details {
    width: 100% !important;
  }
  .MySkills,
  .MyProjects {
    width: 100px !important;
  }
  .MySkills-Details {
    margin-top: 20px !important;
  }
  .MySkill {
    min-width: 90px !important;
    margin: 10px 0 !important;
    padding: 10px !important;
  }
}
